* {
  box-sizing: border-box;
}

main {
  background: linear-gradient(90deg,
      #0b0b0b,
      #ff312e);
  padding-bottom: 55px;

}

:root {
  --primary: #212529;
  --secondary: #343a40;
  --tertiary: #495057;
  --light: #fafafa;
  --dark: #000000;
  --darker: #111111;
  --error: rgb(228, 46, 1);
  --spacing-one: calc(1rem * 0.5);
  --spacing-two: calc(1rem);
  --spacing-five: calc(2.5rem);

}


html,
body {
  min-height: 100%;
}

/* body section */
body {
  margin: 0;
  padding: 0;
  font-weight: 600;
  line-height: 1.5;
  font-size: 18px;
  letter-spacing: .1em;


}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: 'Roboto', sans-serif;
  font-weight: 500;
  margin: 5px 0;
  line-height: 1.25;
}

h1 {
  font-size: 2.25rem;
  color: white;
}

h2 {
  font-size: 2rem;
  padding: 0 20px;
  color:white;
}

h3 {
  font-size: 1.75rem;
  color: white;
}

h4 {
  font-size: 1.5rem;
}

h5 {
  font-size: 1.25rem;
  padding-left: 30px;
  padding-bottom: 10px;
  text-shadow: 0px 4px 3px rgba(0, 0, 0, .4),
    0px 8px 13px rgba(0, 0, 0, .1),
    0px 18px 23px rgba(0, 0, 0, .1);
}

h6 {
  font-size: 1rem;
  color: white;
}

p {
  font-size: 1.1rem;
  margin-top: 0;
  
  padding: 10px;
}

a {
  color: var(--tertiary);
  text-decoration: none;
  font-weight: bold;

}

/* footer */
footer {
  width: 100%;
  height: 100px;
  position: sticky;
  bottom: 0;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  background: linear-gradient(90deg,
      #3b3c3d,
      #afb1b3);
}

.carousel-item {
  height: 30rem;

}

