.card {
  flex: 40%;
  width: 50%;
  margin: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.card:hover {
  box-shadow: 5px 10px 20px 1px rgba(0,0,0,0.253)!important;
  
}

.img-class {
  height: 50px;
  width: 50px;

}

.portfolio-div {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
}

.portfolio-div {
  overflow: hidden;
}

.resume-div {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}

/* card body */
.card-body {
  display: flex;
  align-items: center;
  flex-direction: column;
}

.img-col {
  display: flex;
  justify-content: center;
  align-items: center;
  padding-top: 15px;
  height: 300px;
  flex: 10 15;
}

.rounded {
  transform: scale(1);
	transition:transform 0.6s ease;
}

.bg-dark-text-white {
	overflow: hidden;
}

.rounded:hover {
  transform:scale(1.1);
}

#contact-section {
  padding: 15px;
  border: 1px solid rgba(0, 0, 0, 1);
  border-radius: 5px;
}

.card-text:last-child {
  margin-bottom: 10px;
  text-align: center;
}

.ProjectCard {
  opacity: .5;
}

.ProjectCard:hover {
  opacity: 1;
}

.image-box {
  display: flex;
  justify-content: center;
}

/* cover image */
.cover-image {
  align-self: center;
  max-height: 200px;
  border-radius: 10px;
  margin: 10px;
}

.my-project {
  margin-bottom: 0;

}

#nav-selected {
  color: white;
}

.center {
  text-align: center;
}


.btn {
  margin: 10px
}

/* Github section */
.github-link {
  opacity: .5;
}

.github-link:hover {
  opacity: 1;
}

a:hover {
  text-decoration: none;
}

/* header */
header {
  background: linear-gradient(90deg,
      #2e2f30,
      #7f8182);

  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  position: sticky;
  position: -webkit-sticky;
  top: 0;
  padding: 0.5rem;
  text-align: center;
  width: 100%;
  /* border-bottom: 2px solid #0d3b58; */
  

}

header a {
  color: var(--light);

}

header nav {
  margin: 7px 0;
  color: white;
}

header h2 {
  text-shadow: 0 0 10px rgba(0, 0, 0, 0.5);

}

header p {
  padding: 0 20px;
  color: white;
}

header nav ul {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: right;
  list-style: none;
  text-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
}

header nav ul :hover {
  opacity: .8;
}

/* button */
button {
  font-size: 1rem;
  text-decoration: none;
  padding: 0.25rem 1rem;
  border-radius: .5rem;
  border: none;
  outline: none;
  cursor: pointer;
  margin: 0 1px;
  background-color: var(--secondary);
}

button:hover {
  background-color: #eb9c34;
}

button[disabled] {
  cursor: default;
  opacity: .8;
}

img {
  max-width: 100%;
}

span[role="img"] {
  cursor: default;
}

form {
  width: 40%;
}

input {
  font-size: 1rem;
  padding: 0.25rem;
  border: 1px solid var(--dark);
  outline: none;
  border-radius: 3px;
  transition: box-shadow 0.2s;
}

input:focus {
  border: 1px solid var(--secondary);
  box-shadow: 0 0 .25rem rgba(0, 0, 0, .5);
}

textarea {
  width: 600px;
  height: 120px;
  border: 1px solid var(--dark);
  outline: none;
  border-radius: 3px;
  transition: box-shadow 0.2s;
  padding: 5px;
}

li {
  list-style: none;
}

nav a:hover {
  text-decoration: none;
  opacity: .5;
}

nav {
  padding-top: 15px;
  font-size: 1.25rem;
  font-weight: bold;
  font-family: 'Abel', sans-serif;
}

nav li {
  cursor: pointer;
}

.container {
  width: 85%;
  max-width: 1300px;
  margin-left: auto;
  margin-right: auto;
}

.flex-row {
  display: flex;
  flex-wrap: wrap;
}

.space-between {
  justify-content: space-between;
}

.flex-end {
  justify-content: flex-end;
}

.mx-1 {
  margin-right: var(--spacing-one);
  margin-left: var(--spacing-one);
}

.mx-2 {
  margin-right: var(--spacing-two);
  margin-left: var(--spacing-two);
}

.my-1 {
  margin-top: var(--spacing-one);
  margin-bottom: var(--spacing-one);
}

.my-2 {
  margin-top: var(--spacing-two);
  margin-bottom: var(--spacing-two);
}

.my-5 {
  margin-top: var(--spacing-five);
  margin-bottom: var(--spacing-five);
  padding: 0 20px;
}

.px-1 {
  padding-right: var(--spacing-one);
  padding-left: var(--spacing-one);
}

.px-2 {
  padding-right: var(--spacing-two);
  padding-left: var(--spacing-two);
}

.py-1 {
  padding-top: var(--spacing-one);
  padding-bottom: var(--spacing-one);
}

.py-2 {
  padding-top: var(--spacing-two);
  padding-bottom: var(--spacing-two);
}

.card {
  width: 25%;
  text-align: center;
}

.img-thumbnail {
  width: auto;
  height: 300px;
  margin-bottom: 50px;
  cursor: pointer;
}

/* modal section */
.modalBackdrop {
  background: rgba(68, 64, 64, 0.65);
  bottom: 0;
  left: 0;
  overflow: auto;
  position: fixed;
  right: 0;
  top: 0;
  z-index: 1;
}

.modalContainer {
  background: var(--darker);
  border-radius: 5px;
  max-width: 100%;
  margin: 50px auto;
  padding: 15px;
  width: 960px;
}

.modalTitle {
  font-size: 18px;
  font-weight: 600;
  margin-bottom: 25px;
}

button {
  background: var(--primary);
  border: 0;
  border-radius: 5px;
  color: #fff;
  cursor: pointer;
  font-size: 16px;
  font-weight: 600;
  margin-bottom: 30px;
  padding: 10px 15px;
}

a {
  border-radius: 5px;
  color: var(--primary);
  font-weight: 600;
  padding: 2px 4px;
  margin-left: -2px;
  margin-right: -2px;

}

.navActive {
  color: var(--primary);
}